}

html, body {
	margin:0;
	padding:0;
}

body {
	background:#F6F7F8;
}

.wave {
	position:relative;
	width:100px;
	margin-left: auto;
    margin-right: auto;
}
.dot1 {
    display:inline-block;
    width:12px;
    height:12px;
    border-radius:50%;
    margin-right:3px;
    background:white;
    -webkit-animation: wave 1.3s linear infinite;
            animation: wave 1.3s linear infinite;	
}

.dot2{
    display:inline-block;
    width:12px;
    height:12px;
    border-radius:50%;
    margin-right:3px;
    background:white;
    -webkit-animation: wave 1.3s linear infinite;
            animation: wave 1.3s linear infinite;	
    -webkit-animation-delay: -1.1s;	
            animation-delay: -1.1s; 
}

.dot3{
    display:inline-block;
    width:12px;
    height:12px;
    border-radius:50%;
    margin-right:3px;
    background:white;
    -webkit-animation: wave 1.3s linear infinite;
            animation: wave 1.3s linear infinite;	
    -webkit-animation-delay: -0.9s;	
            animation-delay: -0.9s; 
}

@-webkit-keyframes wave {
	0%, 60%, 100% {
		-webkit-transform: none;
		        transform: none;
		-webkit-transform: initial;
		        transform: initial;
	}
    
	30% {
		-webkit-transform: translateY(-15px);
		        transform: translateY(-15px);
	}
}

@keyframes wave {
	0%, 60%, 100% {
		-webkit-transform: none;
		        transform: none;
		-webkit-transform: initial;
		        transform: initial;
	}
    
	30% {
		-webkit-transform: translateY(-15px);
		        transform: translateY(-15px);
	}
}
.chats {
  min-height: 50px;
  margin: 22px;
  color: white;
  display: flex;
}

.chat-right {
  flex-direction: row-reverse;
}

.chatBox {
  height: 55%;
  width: 90%;
  position: absolute;
  top: 10%;
  left: 0;
  right: 0;
  margin: auto;
  background: #1f1f1f;
  border-radius: 8px;
  padding: 5px 10px;
  overflow: auto;
  box-shadow: 0 0 20px 5px rgba(0, 0, 0, 0.2);
  max-width: 600px;
}
.chat-msg-right {
  margin-right: 20px;
}
.chats-msg {
  height: 100%;
  width: auto;
  list-style-type: none;
  margin-left: 10px;
  margin-bottom: 20px;
  border-radius: 16px;
  padding: 10px;
  font-family: Quicksand;
  font-size: 14px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #d3d3d3;
  max-width: 400px;
  background-color: #404040;
}
.chats-msg-right {
  height: 100%;
  width: auto;
  text-align: end;
  margin-right: 20px;
  margin-bottom: 20px;
  border-radius: 16px;
  padding: 10px;
  font-family: Quicksand;
  font-size: 14px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #d3d3d3;
